<template>
    <el-main>
<!--搜索-->
      <el-row :gutter="20">
        <el-col :span="3"><div class="grid-content bg-purple">
          <el-input v-model="search.cate_name" placeholder="分类名称"></el-input>
          </div>
        </el-col>
      
        <el-col :span="3"><div class="grid-content "><el-button type="primary" @click="onSearch">查询</el-button></div></el-col>
        <el-col :span="6"><div class="grid-content"></div></el-col>
        <el-col :span="6"><div class="grid-content"></div></el-col>
        <el-col :span="6"> <router-link :to="{path:'/editcate',query:{cate_id:0}}"><el-button type="primary">添加</el-button></router-link></el-col>
      </el-row>
<!--列表-->
      <el-table :data="tableData">
        <el-table-column prop="cate_id" label="ID" width="140">
        </el-table-column>
        <el-table-column prop="cate_name" label="商品分类" width="120">
        </el-table-column>
         <el-table-column prop="cate_sort" label="排序">
        </el-table-column>
        <el-table-column label="操作">
           <template slot-scope="scope">
            <router-link :key="scope.row.cate_id" :to="{path:'/editcate',query:{cate_id:scope.row.cate_id}}"> <i class="el-icon-edit"></i></router-link>
            <el-button type="text" @click="delcate(scope.row.cate_id)"><i class="el-icon-delete"></i></el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-count="pageCount"
      :page-size="pageSize"
      layout="total, prev, pager, next, jumper"
      :total="tableNum">
    </el-pagination>
    </el-main>
    
</template>


<script>
  export default {
       data() {
          return { 
            search:{},       
            tableData:[],
            input:'',
            currentPage: 1,
            tableNum:1,
            pageCount:1,
            pageSize:1,
          }

        },
        //初始加载
       created(){
           this.shoplist()
       },
       methods:{
         //查询
         onSearch(page=1) {
           this.$axios({
             method:'post',
             url:this.url() + '/index.php/cate/list',
             params :{
               page:page,
               search:this.search
             }
           }).then(res=>{
              this.tableData=res.data.data.list;
              this.currentPage=res.data.data.paginate.current_page;
              this.tableNum=res.data.data.paginate.record_count;
              this.pageSize=res.data.data.paginate.page_size;
              this.pageCount=res.data.data.paginate.page_count;
              //console.log(res.data.data.paginate);   

            });     
         },
         //商品列表
          shoplist(page=1) {
           this.$axios({
             method:'post',
             url:this.url() +'/index.php/cate/list',
             params :{
               page:page,
             }
           
           }).then(res=>{
              //this.tableData.push(this.form);
              //this.tableData=this.form;
             res.data.data.list.forEach(result => {
               if(result.is_on_sale==1){
                  //console.log(result.is_on_sale);   
                 result.is_on_sale='是';
               }else{
                 result.is_on_sale='否';
               }              
             });   
              this.tableData=res.data.data.list;
              this.currentPage=res.data.data.paginate.current_page;
              this.tableNum=res.data.data.paginate.record_count;
              this.pageSize=res.data.data.paginate.page_size;
              this.pageCount=res.data.data.paginate.page_count;
              //console.log(res.data.data.paginate);   

            });     
         },
          //删除弹框       
          delcate(cate_id) 
          {
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning',
              center: true
            }).then(() => {
              this.$axios({
                method: 'post',
                url: this.url() +'/index.php/cate/del',
                params: {
                  cate_id:cate_id,
                }
              }).then(
                res=>{
                  this.$message({
                      type: 'success',
                      message: '删除成功!'
                    });
                    this.shoplist();
                  //console.log(res);
                },
                res=>{
                // console.log(222);
                },
              );
              }).catch(() => {
                this.$message({
                  type: 'info',
                  message: '已取消删除'
                });
              });
          },
          //分页
          handleCurrentChange(val) {
            this.shoplist(val);
            //console.log(`当前页: ${val}`);
          }
       
      }


  }
</script>

<style>
  .el-row {
    margin-bottom: 20px;
  }
  .el-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple {
    background: #d3dce6;
  }
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
</style>


